<template>
  <div class="slider" ref="slider">
    <div class="slider-group" ref="sliderGroup">
      <slot>
      </slot>
    </div>
    <div class="dots">
      <span class="dot" v-for="(item,index) in dots" :class="{active:currentPageIndex===index}"></span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  import {addClass} from 'common/js/dom.js'
  export default{
      data(){
          return {
              dots:[],
              currentPageIndex:0
          }
      },
      props:{
          loop:{
              type:Boolean,
              default:true,
          },
        autoPlay:{
              type:Boolean,
             default:true
        },
        interval:{
              type:Number,
              default:4000
        }
      },
    mounted(){
       setTimeout(() => {
           this._setSliderWidth();
           this._initDots();
           this._initSlider();
           if(this.autoPlay)
           {
             this._play();
           }
       },20)
      window.addEventListener('resize',() => {
          if(!this.slider) return
          this._setSliderWidth(true);
          this.slider.refresh();
      })
    },
    methods:{
        _setSliderWidth(isResize){
          this.children=this.$refs.sliderGroup.children;
          let width=0;
          let sliderWidth=this.$refs.slider.clientWidth;
          for(let i=0;i<this.children.length;i++)
          {
             let child=this.children[i];
             addClass(child,'slider-item');
             child.style.width=sliderWidth+'px';
             width+=sliderWidth;
          }
          if(this.loop&&!isResize){
              width+=2*sliderWidth;//为了循环轮播,只在初始化时添加两个div但是后面再调整页面大小的时候不需要
          }
          this.$refs.sliderGroup.style.width=width+'px';/*设置轮播容器的宽度*/
        },
        _initSlider(){
            this.slider=new BScroll(this.$refs.slider,{
               scrollX:true,
               scrollY:false,
               momentum:false,/*滑动很快时是否开启惯性滑动*/
               snap:true,
               snapLoop:this.loop,
               snapThreshold:0.3,
               snapSpeed:400,
            });
          this.slider.on('scrollEnd',() => {
              let pageIndex=this.slider.getCurrentPage().pageX;
              if(this.loop)
              {
                  pageIndex-=1;
              }
            this.currentPageIndex=pageIndex;/*由于循环轮播，前面会多一个元素pageX的索引是从1开始的*/
            clearTimeout(this.timer);/*轮播图触发事件时将定时器清除，有可能会手动拖动导致问题*/
            this._play();
          })
      },
        _initDots(){
          this.dots=new Array(this.children.length);
      },
        _play(){
            let pageIndex=this.currentPageIndex+1;
            if(this.loop)
              {
               pageIndex+=1;
               this.timer=setTimeout(() => {
                   this.slider.goToPage(pageIndex,0,400);
               },this.interval)
            }
      },
    },
    destroyed(){
        clearTimeout(this.timer);
    }
  };
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slider
    min-height: 1px
    .slider-group
      position: relative
      overflow: hidden
      white-space: nowrap
      .slider-item
        float: left
        box-sizing: border-box
        overflow: hidden
        text-align: center
        a
          display: block
          width: 100%
          overflow: hidden
          text-decoration: none
        img
          display: block
          width: 100%
    .dots
      position: absolute
      right: 0
      left: 0
      bottom: 12px
      text-align: center
      font-size: 0
      .dot
        display: inline-block
        margin: 0 4px
        width: 8px
        height: 8px
        border-radius: 50%
        background:rgba(144,144,144,.8)
        &.active
          background: #fff
</style>



